import axios from '@/axios.js'

var api = {
    searchHotWords(params) {
        return axios.get('/api/meituan/header/searchHotWords.json', params) //热搜词接口
    },
    getSearchList(params) {
        return axios.get('/api/meituan/header/search.json', params) //搜索关键词接口
    },
    getMenuList() {
        return axios.get('/api/meituan/index/nav.json') //获取左边导航接口
    },
    resultsByKeywords() {
        return axios.get('/api/meituan/index/resultsByKeywords.json') //获取有格调部分
    },
    getProductsList() {
        return axios.get('/api/meituan/list/goodsList.json') //获取商品列表接口
    },
    classify() {
        return axios.get('/api/meituan/list/classify.json') //获取分类接口
    },
    areaList() {
        return axios.get('/api/meituan/list/areaList.json') //获取区域列表接口
    },
    getHotCity() {
        return axios.get('/api/meituan/city/hot.json') //获取热门城市
    },
    getRecentCity() {
        return axios.get('/api/meituan/city/recents.json') //获取热门城市
    },
    getProvinceList() {
        return axios.get('/api/meituan/city/province.json') //获取省份列表
    },
    getCurPosition() {
        return axios.get('/api/meituan/city/getPosition.json') //获取当前位置信息
    },
    getCityList() {
        return axios.get('/api/meituan/city/cityList.json') //获取城市列表
    },
    login(params) {
        return axios.get('/api/meituan/login', params) //登录接口
    },
    register(params) {
        return axios.get('/api/meituan/register', params) //登录接口
    }

}

export default api;


